.footer-links__navigation-link {
  position: relative;
  padding: 3px 8px 1px;
  display: flex;
  color: #ffffff;
  transition: all 0.2s ease;
}
.footer-links__navigation-link::before {
  position: absolute;
  top: -1px;
  left: -1px;
  display: none;
  width: calc(100% + 1px);
  height: calc(100% + 4px);
  background: transparent;
  border: 1px solid #ffffff;
  content: "";
}
.footer-links__navigation-link::after {
  position: absolute;
  bottom: -4px;
  left: 8px;
  display: none;
  width: calc(100% - 16px);
  height: 1px;
  background: #ffffff;
  content: "";
}
@media (hover: hover) {
  .footer-links__navigation-link:hover:not(.focus-visible)::after {
    display: block;
  }
}
.footer-links__navigation-link.focus-visible:focus {
  outline: none;
}
.footer-links__navigation-link.focus-visible:focus::before {
  display: block;
}
.footer-links__navigation-link:active::before {
  display: none;
}
.footer-links__navigation-link:active::after {
  display: none;
}
.footer-links__navigation-link:active:hover:focus::before {
  display: none;
}
.footer-links__navigation-link:active:hover:focus::after {
  display: none;
}
.footer-links__navigation-link--disabled {
  opacity: 0.4;
}
@media (max-width: 1365px) {
