  .footer-links__navigation-link--displaced {
    transform: translateX(1px);
    margin-right: 2px;
  }
  .footer-links__navigation-link::before {
    top: -2px;
    left: -3px;
    width: calc(100% + 6px);
    height: calc(100% + 5px);
  }
  .footer-links__navigation-link::after {
    left: 6px;
    width: calc(100% - 12px);
  }
}
@media (max-width: 767px) {
  .footer-links__navigation-link {
    padding: 3px 6px 0 0;
  }
  .footer-links__navigation-link--displaced {
    transform: translate(0);
    margin: 0;
  }
  .footer-links__navigation-link::before {
    top: -1px;
    left: -9px;
    width: calc(100% + 12px);
    height: calc(100% + 5px);
  }
  .footer-links__navigation-link::after {
    left: 0;
    bottom: -5px;
    width: calc(100% - 6px);
  }
}

